/* Uc style */

.btn {
    font-size: 16px;
    font-weight: bold;
    min-width: 140px;
    border-radius: 3px;
}

.text-primary {
    color: #19aa8d !important;
}

.badge {
    white-space: normal;
}

.badge-primary {
    color: #19aa8d;
    border: 1px solid #19aa8d;
    background: transparent;
}

.bg-primary {
    background-color: #19aa8d!important;
}

.btn-primary {
    color: #fff;
    background-color: #19aa8d;
    border-color: #19aa8d;
}

.btn-primary:hover {
    color: #19aa8d;
    background-color: #fff;
    border-color: #19aa8d;
}

.btn-primary.focus,
.btn-primary:focus {
    box-shadow: 0 0 0 0.2rem rgba(25, 170, 141, 0.5);
}

.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):active,
.show>.btn-primary.dropdown-toggle {
    color: #fff;
    background-color: #19aa8d;
    border-color: #19aa8d;
}

.btn-primary:not(:disabled):not(.disabled).active:focus,
.btn-primary:not(:disabled):not(.disabled):active:focus,
.show>.btn-primary.dropdown-toggle:focus {
    box-shadow: 0 0 0 0.2rem rgba(25, 170, 141, 0.5);
}

